html {
    @include box-sizing(border-box);
}

*,
*:after,
*:before {
    @include box-sizing(inherit);
}

body {
    font-family: $main_source;
    background-color: $light;
}

.hv-40 {
    height: 40vh;
}

.hv-45 {
    height: 45vh;
}

.hv-50 {
    height: 50vh;
}

.hv-55 {
    height: 55vh;
}

.hv-60 {
    height: 60vh;
}

.hv-65 {
    height: 65vh;
}

.hv-70 {
    height: 70vh;
}

.navbar-brand {
    max-width: 70%;
    @include medium {
        max-width: 100%;
    }
}

.btn {}

.btn-light {
    color: $light;
    position: relative;
    background: transparent;
    border-radius: 10px;
    border: 2px solid transparent;
    background: linear-gradient(#162d66, #162d66) padding-box, linear-gradient(95deg, #4C84FF 15.4%, #85FFFF 102.57%) border-box;
    &:hover,
    &:focus {
        background-color: darken($light, 5);
        color: $light;
    }
}

.btn-success {
    color: $light;
    background-color: #23D160;
}

.btn-primary {
    background: linear-gradient(110.42deg, #4C84FF 15.4%, #85FFFF 102.57%);
    box-shadow: 8px 6px 20px rgba(76, 132, 255, 0.5);
    border-radius: 10px;
    &:hover,
    &:focus {
        background-color: darken(#4C84FF, 8);
    }
}

.form-control-lg {
    background: #F5F7FB;
    /* azul dame coins */
    border: 1px solid #23509d80;
    box-sizing: border-box;
    border-radius: .5rem;
    font-size: 12px;
    color: #C7C7C7;
    height: calc(1.5em + 1.7rem + 2px);
    @include medium {
        font-size: 15px;
        height: calc(1.5em + 1.4rem + 2px);
    }
}

// Header
.header {
    position: relative;
    z-index: 100;
    overflow: hidden;
    min-height: 100vh;
    height: 1920px;
    @include medium {
        height: 1680px;
    }
    @include large {
        height: 1100px;
    }
    &::before {
        content: '';
        position: absolute;
        width: 2286.32px;
        height: 1367.09px;
        left: -520px;
        top: -438.81px;
        background: linear-gradient(91.48deg, #3552A2 -0.6%, #09173E 56.01%);
        border-radius: 0px 0px 0px 300px;
        transform: rotate(-38.68deg);
        @include medium {
            top: -738.81px;
        }
    }
    .container-fluid {
        width: 90%;
        &:first-child {
            &::before {
                content: '';
                position: absolute;
                width: 1152.74px;
                height: 872.55px;
                left: 674.75px;
                top: -125.4px;
                background: #4C84FF;
                opacity: 0.21;
                border-radius: 45px;
                transform: rotate(47.34deg);
            }
        }
    }
    .nav-item {
        .nav-link {
            color: $light;
            &:hover {
                color: darken($light, 6);
                text-decoration: underline;
            }
        }
        .btn {
            width: 180px;
            @include large {
                width: 120px;
            }
        }
        .btn-light {
            img {
                max-height: 24px;
            }
        }
        .btn-primary {
            width: 180px;
            padding: .45rem .75rem;
        }
    }
    .buy-cryptos {
        margin-top: 2rem;
        @include extra-large {
            margin-top: 5rem;
        }
        .buy-crypto {
            color: $light;
            span {
                color: $green;
            }
            font-size: 1.8rem;
            @include small {
                font-size: 2.2rem;
            }
            @include medium {
                font-size: 2.6rem;
            }
            @include large {
                font-size: 3rem;
            }
        }
        .about {
            font-size: 20px;
            color: $light;
            font-weight: normal;
            @include medium {
                font-size: 24px;
            }
        }
        .register-button {
            .btn {
                padding: .5rem .75rem;
                border-radius: .70rem;
                @include large {
                    padding: .75rem .75rem;
                    font-size: 1.35rem;
                }
            }
        }
        .steps {
            position: relative;
            &:before {
                display: block;
                height: 60%;
                content: '';
                position: absolute;
                left: 0;
                border-left: 1px dashed $light;
                margin-top: .7rem;
                @include small {
                    height: 64%;
                }
                @include large {
                    height: 60%;
                }
                @include extra-large {
                    height: 64%;
                }
            }
            .step {
                margin-bottom: 1rem;
                list-style: none;
                position: relative;
                color: $light;
                font-weight: 300;
                &:before {
                    position: absolute;
                    content: '';
                    margin-top: .3rem;
                    width: 16px;
                    height: 16px;
                    border-radius: 50%;
                    left: -3rem;
                    background: linear-gradient(180deg, #D7EEDF 0%, #23D160 100%);
                }
            }
        }
    }
    .form-content {
        position: relative;
        &::before {
            content: '';
            background: linear-gradient(95.34deg, #4070F4 21.35%, #23509D 93%);
            opacity: 0.21;
            border-radius: 45px;
            transform: rotate(47.34deg);
            position: absolute;
            width: 264.48px;
            height: 268.94px;
            right: -80px;
            top: 290px;
            z-index: 1;
        }
        .form-crypto {
            background-color: $light;
            border-radius: 2rem;
            padding: 1.5rem;
            position: relative;
            z-index: 100;
            height: 100%;
            box-shadow: 28px 24px 83px rgba(22, 40, 94, 0.45);
            border-radius: 35px;
            @include medium {
                padding: 3rem 1.5rem;
            }
            .small {
                color: $tertiary;
            }
            label {
                color: $tertiary;
                font-weight: 500;
                font-size: 18px;
            }
            input[type=number]::-webkit-inner-spin-button,
            input[type=number]::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
            input[type=number] {
                -moz-appearance: textfield;
                position: relative;
            }
            .specific-amount {
                position: relative;
                &:after {
                    @include large {
                        content: '';
                        position: absolute;
                        bottom: 2.8rem;
                        right: -5rem;
                        width: 39.5%;
                        height: 1px;
                        background-image: linear-gradient(to right, transparent 50%, #223049 50%);
                        background-size: 3px 100%;
                    }
                }
            }
            .form-control-lg-amount {
                padding-right: 7rem;
            }
            .bootstrap-select {
                position: absolute;
                right: .3rem;
                top: 2.2rem;
                width: 40%;
                &.show {
                    .filter-option-inner-inner {
                        color: $light;
                        &:hover,
                        &:focus {
                            color: $light;
                        }
                    }
                    .dropdown-toggle {
                        &::after {
                            transform: rotate(-180deg);
                        }
                    }
                }
                .dropdown-item.active {
                    background-color: #162d66;
                    &:focus {
                        background-color: #162d66;
                    }
                    &:hover {
                        color: darken($light, 5);
                    }
                }
                .btn-light {
                    background: linear-gradient(#162d66, #162d66) padding-box;
                    border: none;
                    border-radius: 8px;
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                    height: 46px;
                }
                .dropdown-toggle {
                    &::after {
                        border: none;
                        content: '';
                        width: 26px;
                        height: 12px;
                        background-image: url(../img/icons/chevron-arrow-down.png);
                        background-repeat: no-repeat;
                        transition: all .5s ease;
                    }
                    .filter-option-inner-inner {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        height: 2.2rem;
                        line-height: 1.3;
                        small {
                            line-height: 1;
                            margin-bottom: .2rem;
                            color: $light !important;
                        }
                    }
                }
            }
            .form-text {
                color: #464646;
                font-weight: 300;
            }
            .center-label {
                position: relative;
                &:before {
                    content: '';
                    position: absolute;
                    border: 0.5px solid #CDCDCD;
                    left: 0.5rem;
                    width: 94.5%;
                    top: .85rem;
                    @include medium {
                        width: 97.5%;
                    }
                    @include large {
                        left: .95rem;
                        width: 93.5%;
                    }
                }
                label {
                    background-color: $light;
                    padding: 0 1rem;
                    position: relative;
                    z-index: 1;
                }
            }
            .suggested-amount {
                display: grid;
                grid-template-columns: 8% 42% 50%;
                border: 0.5px solid #23509d80;
                box-sizing: border-box;
                border-radius: 10px;
                padding: 0.4rem .5rem;
                margin-bottom: 1rem;
                @include large {
                    grid-template-columns: 12% 40% 50%;
                }
                @include extra-large {
                    grid-template-columns: 10% 40% 50%;
                }
                .form-check-input {
                    position: unset;
                    margin-top: 0;
                    margin-left: 0;
                    background-color: #CFD6EB;
                    /* Dark Blue */
                    border: 1px solid #16285E;
                    box-sizing: border-box;
                    border-radius: 3px;
                }
                .check {
                    align-self: center;
                    input {
                        padding: 0;
                        height: initial;
                        width: initial;
                        margin-bottom: 0;
                        display: none;
                        cursor: pointer;
                        &:checked+.checkmark::after {
                            content: '';
                            display: block;
                            position: absolute;
                            top: 9px;
                            left: 3px;
                            width: 6px;
                            height: 9px;
                            border: solid $dark;
                            border-width: 0 2px 2px 0;
                            transform: rotate(36deg);
                        }
                    }
                    .checkmark {
                        position: relative;
                        cursor: pointer;
                        &:before {
                            content: '';
                            -webkit-appearance: none;
                            background-color: #CFD6EB;
                            border: 1px solid #16285E;
                            border-radius: 3px;
                            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
                            padding: 5px;
                            display: inline-block;
                            position: relative;
                            vertical-align: middle;
                            cursor: pointer;
                            margin-right: 5px;
                        }
                    }
                }
                .amounts,
                .receives {
                    .title {
                        font-weight: 300;
                        font-size: 14px;
                        color: $tertiary;
                        @include medium {
                            font-size: 13px;
                        }
                        @include large {
                            font-size: 11px;
                        }
                    }
                }
                .amounts {
                    margin-right: .2rem;
                }
                .amount,
                .crypto-amount {
                    color: $tertiary;
                    font-weight: bold;
                    font-size: 18px;
                    @include medium {
                        font-size: 16px;
                    }
                    @include large {
                        font-size: 14px;
                    }
                }
                .amount {
                    position: relative;
                    &::after {
                        @include extra-large {
                            display: block;
                            content: '';
                            background-image: url(../img/amount-line.png);
                            // width: calc(4rem - .5rem);
                            background-size: calc(2rem - .5rem);
                            width: 1.5rem;
                            height: 2px;
                            top: 45%;
                            right: 0;
                            background-repeat: no-repeat;
                            position: absolute;
                        }
                    }
                }
            }
            .credit-card {
                display: flex;
                width: 100%;
                input[type="email"] {
                    padding-left: 3rem;
                    // border-radius: .25rem;
                    background-image: url(../img/icons/email.png);
                    background-repeat: no-repeat;
                    background-position: calc(1% + .5rem) calc(1% + .75rem);
                }
            }
            .input-cards {
                display: grid;
                grid-template-columns: 60% repeat(2, 20%);
                .card-number {
                    padding-left: 3rem;
                    // border-radius: .25rem;
                    background-image: url(../img/icons/credit-card.png);
                    background-repeat: no-repeat;
                    background-position: calc(1% + .7rem) calc(1% + .75rem);
                    border-right: none;
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                }
                .card-my {
                    border-radius: 0;
                    border-right: none;
                    padding: .5rem .2rem;
                    text-align: center;
                    @include small {
                        padding: .5rem .8rem;
                    }
                    @include large {
                        padding: .5rem .2rem;
                    }
                    @include extra-large {
                        padding: .5rem .8rem;
                    }
                }
                .card-cvv {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                    text-align: center;
                    padding: .5rem .2rem;
                    @include small {
                        padding: .5rem .8rem;
                    }
                    @include large {
                        padding: .5rem .2rem;
                    }
                    @include extra-large {
                        padding: .5rem .8rem;
                    }
                }
            }
            .btn-success {
                padding: .5rem 1rem;
                box-shadow: 5px 8px 25px rgba(10, 132, 53, 0.41);
                border: none;
                border-radius: .5rem;
                @include large {
                    padding: 1rem;
                }
            }
        }
    }
}